

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

th, td {
  text-align: left;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f2f2f2;
  color: #333;
  font-size: 25px;
}

td:first-child {
  font-weight: bold;
}

/* Style for genre filter component */
.genre-filter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.genre-filter button {
  padding: 10px;
  margin: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.genre-filter button:hover {
  background-color: #0069d9;
}

/* App */
.app {
  padding: 2rem;
}

/* MovieList */
.movie-list {
  margin-top: 2rem;
  border-collapse: collapse;
  width: 100%;
  background-color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.movie-list td, .movie-list th {
  border: 1px solid #ddd;
  padding: 0.75rem;
}

.movie-list th {
  text-align: left;
  background-color: var(--primary-color);
  color: white;
}

.movie-list tr:nth-child(even) {
  background-color: #f2f2f2;
}

/* GenreFilter */
.genre-filter {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.genre-filter button {
  background-color: var(--button-color);
  color: var(--text-color);
  border: none;
  margin-right: 1rem;
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  cursor: pointer;
}

.genre-filter button:hover {
  background-color: var(--button-hover-color);
}

.genre-filter button:active {
  background-color: var(--button-active-color);
}

.genre-filter button.active {
  background-color: var(--secondary-color);
  color: white;
}

button {
  background-color: #0b4f93; /* set background color */
  color: white; /* set text color */
  padding: 12px 24px; /* add padding */
  margin: 3px;
  font-size: 16px; /* set font size */
  border: none; /* remove border */
  border-radius: 4px; /* add border radius */
  cursor: pointer; /* change cursor on hover */
}

button:hover {
  background-color: #10233b; /* change background color on hover */
}
.span{
  font-size: 30px;
  font-weight: bolder;
  margin: 20px;
}